import { Alert, message, Radio } from 'antd';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import firebase from '../firebase';
import jwtDecode from 'jwt-decode';


const TestApp = () => {
    
     var d = new Date();
    let gio = d.getHours();
    let phut = d.getMinutes();
    let gkt = 14;
    let pkt = 0;
    let date = 0;
    const [ minutes, setMinutes ] = useState(0);
    const [seconds, setSeconds ] =  useState(0);
    const [dataBT, setDataBT] = useState([]);
    const [todoList, setTodoList] = useState([]);
    const [listQuestion, setListQuestion] = useState([]);
    const params = useParams();
    useEffect(()=>{
        const getDataById = async (id) =>{
            const todoRef = await firebase.database().ref('test').child(id);
            //   console.log(todoRef);
            todoRef.on('value', (snapshot) => {
                const todos = snapshot.val();
                setTodoList(todos);       
              });
            
        }
        const getListQuestion =  (id) => {
            const todoRef = firebase.database().ref('question').orderByChild('iddethi').equalTo(id);
          todoRef.on('value', (snapshot) => {
            const todos = snapshot.val();
            const data = [];
            for (let id in todos) {
              data.push({ id, ...todos[id] });
            }
            setListQuestion(data);
            
          });
          }
        getDataById(params.id);
        getListQuestion(params.id);
        let batdau = todoList.tgbatdau+'';
          let kethuc = todoList.tgketthuc+'';
          gkt = parseInt(kethuc.substr(0,2));
          pkt = parseInt(kethuc.substr(3,2));
          let gbd = parseInt(batdau.substr(0,2));
          let pbd = parseInt(batdau.substr(3,2));
        const setTimeTest = () => {
           // bđ: 8h00 // kt:8h40
           // = pkt - phut bd
           // > 
           if((gio >= gbd) && (gio <=gkt)){
            if(gkt - gio > 0){
                    let g = (gkt-gio)*60;
                    let p = pkt - phut;
                    date = g + p;
                }
                if(gkt - gio === 0){
                    date = pkt - phut;
                }
           }else{
               date = 0;
           }
    setMinutes(date);
    
        }
        setTimeTest();

        // let ngaybd = todoList.ngaybatdau+'';
        // let ngaykt = todoList.ngayketthuc+'';
        // if(d.getDate()+d.getMonth()+1 < parseInt(ngaybd.substr(0,2))+parseInt(ngaybd.substr(3,2)) ||d.getDate()+d.getMonth()+1 > parseInt(ngaykt.substr(0,2))+parseInt(ngaykt.substr(3,2)) ){
        //     alert('Chua đến thời gian làm bài, hoặc hết thời gian làm bài.')
        //     window.location.href = '/';
    
        //     }
        let myInterval = setInterval(() => {
            if (minutes === 0 || minutes < 0) {
                setMinutes(0);

                clearInterval(myInterval)
            } else{
                if (seconds > 0) {
                setSeconds(seconds - 1);
            } else {
                    setMinutes(minutes - 1);
                    setSeconds(59);
                }
            }
            
        }, 1000)
        return ()=> {
            clearInterval(myInterval);
          };
          
    },[todoList])
    useEffect(()=>{
        
    
    });
    useEffect(() => {
        
        let bthi = localStorage.getItem("BaiThi");
        if(bthi !== null){
            setDataBT(JSON.parse(bthi));
        }
    },[])
    const answer = (e, id) => {
        let answ = localStorage.getItem('BaiThi');
        let obj = {id: id, as: e.target.value};
        if(answ === null){
            const asArr = [];
            asArr.push(obj);
            localStorage.setItem('BaiThi',JSON.stringify(asArr));
        }else{
            answ = JSON.parse(answ);
            let check = true;
            answ.map((key, index) => {
                if(key.id === id){
                    answ[index].as = e.target.value;
                    check = false;
                }
            })
            localStorage.setItem('BaiThi', JSON.stringify(answ));
            if(check){
                answ.push(obj);
                localStorage.setItem('BaiThi', JSON.stringify(answ));
            }
           
        }
         console.log(answ);
    }
    const submitTestOnl = (e) => {
        e.preventDefault();
        let choice = 0;
        let total = listQuestion.length;
        let answ = localStorage.getItem('BaiThi');
        answ = JSON.parse(answ);
        listQuestion.map((as, key) =>{
            answ.map((chon, k) => {
                if(as.id === chon.id){
                    if(as.true === chon.as){
                        choice = choice + 1
                    }
                }
            } )
        })
        let mark = 10/total * choice;
        let data = {
            idbaithi: params.id,
            mssv: decode.mssv,
            hoten: decode.data,
            dung: choice,
            diem: mark
        }
        firebase.database().ref('result').push(data);
        localStorage.setItem(decode.mssv+todoList.id, mark);
        localStorage.setItem('true'+todoList.id, choice);
        window.location.href = '';
    }
    const data = [
    {id: 2, ch: 'sao anh chưa về vậy?', as1: 'Đi nhậu rồi', as2: 'Đi chơi với bồ', as3: 'Đang fix bug', as4: 'Chơi game', true: 'C'},
    {id: 3, ch: 'Không có người yêu thì làm gì?', as1: 'Đi nhậu', as2: 'Đi chơi', as3: 'fix bug', as4: 'Chơi game', true: 'C'},
    {id: 4, ch: 'Giữa bạn gái với fix bug bạn lưa chọn gì?', as1: 'Khong gì cả', as2: 'Làm gì có bạn gái', as3: 'fix bug', as4: 'Chơi game', true: 'C'},
]
let chon = null;
let id = 0;
let token = localStorage.getItem('tokentest');
    let decode = token === null ? {} : jwtDecode(token);
let kt = localStorage.getItem(decode.mssv+todoList.id);
let dung = localStorage.getItem('true'+todoList.id);

    return(
        <>{
            kt !== null ? (
                <div className="container">
                    <h3>Bạn đã làm xong bài thi: {todoList.baithi}</h3>
                    <h3>Số câu đúng: {dung+'/'+todoList.socauhoi}</h3>
                    <h3>Thang điểm: 10</h3>
                    <h3>Điểm: {kt}</h3>
                </div>
            ):(
        
        <div style={{backgroundColor:'#fff',width: '100%'}} >
            <div style={{position: 'fixed', textAlign:'center', top:0, zIndex:10000, background:'darkgray', left:50, right:50}}>
                <h3>MSSV: {decode.mssv} - {decode.data}</h3>
                <h3>{todoList.baithi}</h3>
                <h3>Thời gian thi: {todoList.ngaybatdau + ' '+todoList.tgbatdau+ ' - '+todoList.tgketthuc}</h3>
            <h3>Thời gian làm bài còn: {minutes+':'+seconds} phút {minutes <5 && minutes > 0? (<span style={{color:'red'}}>Sắp hết thời gian làm bài</span>):null}</h3>
            </div>
            
            {((minutes <= 0 & seconds <= 0)) ? (
                <>
                <div style={{marginTop:200-20, textAlign:'center', color:'red'}}>
                    <h3 style={{color:'red'}}>Chưa đến thời gian thi, hoặc đã hết thời gian làm bài.</h3>
                </div>
                
                </>
            ) :
            <>
            <form onSubmit={(e)=>submitTestOnl(e)}>
            <div style={{marginTop:200-20}} className="container">
                <h4 className="text-center">Đề bài</h4>
                <p className="text-center">Cán bộ coi thi không giải thích gì thêm</p>
                <hr/>
            {
            listQuestion.map((key, index) =>{ 
                id ++;
                return(
                <div>
                    {/* { 
                    dataBT ? dataBT.map((asw, index)=>{
                        chon = 0;
                        if(asw.id === key.id){
                            chon = asw.as;
                       return( */}
                           
                   <h4>Câu {id}: {key.cauhoi}</h4>
                   <table className="table table-bordered">
                   <tr>
                       <td>
                            <label><input type="radio" name={'cch'+id} defaultValue='A' onChange={(e)=>answer(e, key.id)} required/>A. {key.as1}</label>
                       </td>
                       <td>
                            <label><input type="radio" name={'cch'+id} defaultValue='B' onChange={(e)=>answer(e, key.id)} required/>B. {key.as2}</label>
                       </td>
                   </tr>
                   <tr>
                       <td>
                            <label><input type="radio" name={'cch'+id} defaultValue='C' onChange={(e)=>answer(e, key.id)} required/>C. {key.as3}</label>
                       </td>
                       <td>
                            <label><input type="radio" name={'cch'+id} defaultValue='D' onChange={(e)=>answer(e, key.id)} required/>D. {key.as4}</label>
                       </td>
                   </tr></table>
            </div>
        )
    })
                       }
                       <button className="btn btn-primary btn-block" type="submit" >Nộp bài</button>
                       </div></form>
        </>
        }
        
        </div>
            )}
             
        </>
    )
}
export default TestApp;